@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap");

body {
  background-image: url(./asserts/images/download.svg);
  background-position: right;
  background-repeat: no-repeat;
  padding: 0;
  margin: 0;
  background-size: 30%;
}

.nav-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 6px 10px 5px -1px rgba(222, 222, 222, 0.75);
  -webkit-box-shadow: 6px 10px 5px -1px rgba(222, 222, 222, 0.75);
  -moz-box-shadow: 6px 10px 5px -1px rgba(222, 222, 222, 0.75);
  background-color: rgb(255, 255, 255);
  margin-bottom: 30px;
}

.nav-items {
  display: flex;
  gap: 50px;
}

.nav-items > li > a {
  text-decoration: none;
}

.nav-items > li > a:hover {
  text-decoration: underline;
}

.sectionOne {
  width: 70%;
  margin: auto;
  display: grid;
  place-items: center;
  text-align: center;
  background-color: rgba(50, 50, 50, 0.04);
  padding: 40px 10px;
  border-radius: 20px;
}

h2 {
  font-weight: 700 !important;
  font-size: 3rem;
  color: rgb(0, 0, 100);
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

h2 span {
  font-weight: 400;
  font-size: 1rem;
}

.wrapper {
  position: relative;
  margin-top: 40px;
}

.wrapper p {
  font-weight: bold;
  margin-bottom: 10px;
}

.input {
  min-width: 50vw;
  height: 35px;
  border-radius: 5px;
  border: 2px #323232 solid;
  padding: 5px 15px;
  background-color: rgba(232, 232, 232, 0.3);

  font-size: 15;
  font-weight: 500;
}

.placeholder {
  width: 65%;
  position: absolute;
  top: -18px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  text-align: center;
  color: blueviolet;
  background-color: rgba(232, 232, 232, 0.9);
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 1.5px;
  transition: all 0.3s;
  opacity: 0;
}

.input::placeholder {
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
}

.input:focus + .placeholder {
  opacity: 1;
}

.input:focus {
  outline: 2px solid blueviolet;
  border: none;
  color: blueviolet;
  background-color: rgba(232, 232, 232, 0.8);
}

.input:focus::placeholder {
  opacity: 0;
}

button {
  border: none;
  color: #fff;
  background-image: linear-gradient(30deg, #4340f8, #4ce3f7);
  border-radius: 20px;
  background-size: 100% auto;
  font-family: inherit;
  font-size: 17px;
  padding: 0.6em 1.5em;
  margin-top: 20px;
}

button:hover {
  background-position: right center;
  background-size: 200% auto;
  -webkit-animation: pulse 2s infinite;
  animation: pulse512 1.5s infinite;
}

@keyframes pulse512 {
  0% {
    box-shadow: 0 0 0 0 #05bada66;
  }

  70% {
    box-shadow: 0 0 0 10px rgb(218 103 68 / 0%);
  }

  100% {
    box-shadow: 0 0 0 0 rgb(218 103 68 / 0%);
  }
}

.font-disp {
  font-size: 2.8125rem;
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #484dfe;
  font-weight: 700;
  min-width: 30%;
  flex-wrap: wrap;
}

.font-disp span {
  font-size: 1rem;
  color: #323232;
  font-weight: 300;
}

.disp {
  max-width: 20%;
}

// @media screen and (width: 1000px) {
// }

@media screen and (max-width: 600px) {
  .nav-items {
    display: none;
  }
  .sectionTwo {
    flex-wrap: wrap;
  }

  .font-disp {
    font-size: 1.8rem;
  }

  .disp {
    max-width: 30%;
  }
}
